import uniRate from '../../components/uni-rate.vue'
import loadMore from '../../components/loadeMore.vue'
var _self, timer = null;

export default {
	data() {
		return {
			ifChecked: true,
			flag: true,
			current: '',
			id: '',
			dayId: '',
			// level:'',
			isBody: 0,
			pageNumber: 1,
			pageSize: 7,
			footData: [],
			loadingText: '加载中...',
			loadingType: 0, //定义加载方式 0---contentdown  1---contentrefresh 2---contentnomore
			contentText: {
				contentdown: '上拉显示更多',
				contentrefresh: '正在加载...',
				contentnomore: '没有更多数据了'
			}
		}
	},
	components: {
		uniRate,
		loadMore
	},
	onReachBottom: function() {

		if (this.flag === true) {
			this.pageNumber++
			_self.req(this.current, '');
		} else {

		}

	},
	onLoad(e) {
		this.token = sessionStorage.getItem('token')
		// console.log(e)
		_self = this
		this.id = e.id
		this.dayId = e.dayId
		this.req(this.current, '')
	},
	methods: {
		setRadio() {
			this.ifChecked = !this.ifChecked
			if (this.ifChecked == true) {
				this.isBody = 1
			} else {
				this.isBody = 0
			}
			this.footData = []
			this.pageNumber = 1
			this.req(this.current);
		},
		//删除评论
		delComment(index, commentId) {
			var that = this;
			var data = JSON.stringify({
				infoId: this.id,
				commentId: commentId,
			})
			uni.showModal({
				title: '提示',
				content: '确定删除该评论吗?',
				success: function(res) {
					if (res.confirm) {
						uni.request({
							url: that.nowUrl + '/foods/comment/removeComment?infoId=' + that.id + '&commentId=' + commentId,
							header: {
								// 'contentType':'application/x-www-form-unlencoded',
								'token': that.token
							},
							method: "POST",
							// data: data,
							success(res) {
								if (res.data.code == 200) {
									uni.showToast({
										duration: 1000,
										content: '删除成功',
									})
									that.footData.splice(index, 1)
								} else {
									uni.showToast({
										duration: 1000,
										content: '失败',
									})
								}
							},
							fail() {
								uni.showToast({
									duration: 1000,
									content: '失败',
								})
							}
						})
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});



		},

		req(level, choosed) {
			if (choosed === 'choose') {
				this.footData = []
				this.pageNumber = 1
			}
			let that = this
			this.current = level
			that.loadingType = 1
			uni.showNavigationBarLoading(); //显示加载动画
			uni.request({
				url: this.nowUrl + '/foods/comment',
				header: {
					'token': this.token
				},
				data: {
					id: this.id,
					level: this.current,
					isBody: this.isBody,
					pageNumber: this.pageNumber,
					pageSize: this.pageSize
				},
				success(res) {
					console.log(res.data.data.list);
					if (res.data.data.list.length < that.pageSize) {
						that.flag = false
						that.loadingType = 2
					} else {
						that.flag = true
						that.loadingType = 1
					}
					that.footData = that.footData.concat(res.data.data.list)
					uni.hideNavigationBarLoading();
					// console.log(that.footData)
					uni.hideLoading()
				}
			})
		}
	}
}
